<template>
	<div class="my-bank-card">
		<mt-navbar fixed v-model="selected">
  			<mt-tab-item id="1">储蓄卡({{AccountList.length}})</mt-tab-item>
  			<mt-tab-item id="2">信用卡(3)</mt-tab-item>
		</mt-navbar>
		<div class="my-bank-card-contain">
			<mt-tab-container v-model="selected">
	  			<mt-tab-container-item id="1">
	    			<bank-card :bank-card-list="accountList" @bank-card-click="bankCardClick" class="contain-card-list"></bank-card>
	    			<div v-show="AccountList.length > accountList.length" class="show-more">
		    			<div class="show-more-line">
		    				<a @click="showMore()" href="javascript:;">
		    					<span>展示更多已添加的卡</span>
		    				</a>
		    			</div>
	    			</div>
	    			<div class="tip-not-add">
	    				{{account.length > 0 ? '未添加到手机银行的账户' : '您的账号已全部添加至手机银行！'}}
					</div>
					<div v-for="item in account" class="bank-card-account">
						<div class="card-account-left">
							<p class="number">{{item.number}}</p>
							<p class="bank">{{item.bank}}</p>
							<p class="money">
								<span>余额：</span>
								<span>{{item.count}}</span>
								<span>元</span>
							</p>
						</div>
						<div class="card-account-right">
							<a @click="addAccount(item.id)" href="javascript:;">
								<i class="add-account-icon"></i><br>
								<span class="add-account-span">添加</span>
							</a>
						</div>
					</div>
					<div class="fixed-footer">
						<mt-button @click.native="addBankCard" size="large" type="primary">
							<img src="../../../assets/img/bank/card/add-white@2x.png" width="18" height="18" slot="icon">手动添加银行卡
							</mt-button>
					</div>
	  			</mt-tab-container-item>
	  			<mt-tab-container-item id="2">
	    			<bank-card :bank-card-list="creditList" @bank-card-click="creditClick" class="contain-card-list"></bank-card>
	    			<div class="fixed-footer">
						<mt-button @click.native="addCredit" size="large" type="primary">
							<img src="../../../assets/img/bank/card/add-white@2x.png" width="18" height="18" slot="icon">手动添加信用卡
						</mt-button>
					</div>
	  			</mt-tab-container-item>
			</mt-tab-container>
			
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';
import card from '@/services/my/public';

export default {
	data() {
		return {
			selected: '1',
			account: [],
			accountList: [],
	        creditList: [],
	        AccountList: [],
	        accountListNum: 10
		};
	},
	methods: {
		addAccount(id){
			this.$router.push({name: 'MyBankCardAddAccount'});
		},
		addBankCard(){
			this.$router.push({name: 'MyBankCardAddCard'});
		},
		addCredit() {
			this.$router.push({name: 'MyBankCardCreditAdd'});
		},
		bankCardClick(item){
			this.$router.push({name: 'MyBankCardSet'});
		},
		creditClick(item){
			console.log(item);
			this.$router.push({name: 'CreditManageCardSet', params: {card: item.account}});
		},
		getBankCardList() {
			service.getBankCardList().then(data => {
				this.AccountList = data.data;
				this.accountList = this.AccountList.slice(0, this.accountListNum);
			});
		},
		getBankCardAccount() {
			service.getBankCardAccount().then(data => {
				this.account = data.data;
			});
		},
		getBankCreditList() {
			service.getBankCreditList().then(data => {
				this.creditList = data.data;
			});
		},
		showMore(){
			this.accountListNum += 10;
			this.accountList = this.AccountList.slice(0, this.accountListNum);
		}
	},
	created(){
		this.getBankCardList();
		this.getBankCardAccount();
		this.getBankCreditList();
		card.getCardList({
			ACCT_TYPE: '1',
			OPT_TYPE: '1'
		}).then((data) => {
			console.log(data);
		}, (err) => {
			console.log(err);
		});
	}
};

</script>

<style lang="scss">

.my-bank-card{
	.mint-navbar{
		.mint-tab-item.is-selected{
			border-bottom: 0.08rem solid #FF511A;
	    	color: #FF511A;
		}
		.mint-tab-item-label{
			font-size: 16px;
			letter-spacing: 0.5px;
		}
	}
	.my-bank-card-contain{
		padding-top: 140px;
		padding-bottom: 190px;
		.contain-card-list{
			padding-left: 30px;
			padding-right: 30px;
		}
		.show-more{
			height: 42px;
			margin: 60px 30px 30px;
			.show-more-line{
			    height: 1px;
			    border-top: 1px dashed #d6d6d6;
			    text-align: center;
			}
			.show-more-line a{
			    position: relative;
			    top: -16px;
			    background: #f3f3f3;
			    font-size: 15px;
			    padding: 0 60px 0 25px;
			}
			.show-more-line a:after{
				content: '';
				position: absolute;
				top: 1px;
				right: 10px;
				display: inline-block;
				width: 19.00px;
				height: 19.00px;
				@include image-set('../../../assets/img/bank/card/show');
				background-size: auto 80%;
			}
		}
	}
	.tip-not-add{
		padding: 30px;
		font-size: 14px;
		color: #272727;
	}
	.bank-card-account{
		padding: 30px;
		height: 210px;
		background-color: #fff;
		.card-account-left{
			float: left;
			.number{
				font-size: 20px;
				color: #272727;
			}
			.bank{
				margin: 16px 0px;
				font-size: 13px;
				color: #B5B5B5;
			}
			.money>{
				span:nth-child(1){
					font-size: 13px;
					color: #272727;
				}
				span:nth-child(2){
					font-size: 15px;
					color: #FF511A;
				}
				span:nth-child(3){
					font-size: 15px;
					color: #272727;
				}
			}
		}
		.card-account-right{
			float:right;
			width: 155.14px;
			margin-top: 28px;
			border-left: 1px solid #E4E5E6;
			text-align: center;
			.add-account-icon{
				display: inline-block;
				width: 48px;
				height: 48px;
				background-size: auto 100%;
				background-repeat: no-repeat;
	            @include image-set('../../../assets/img/bank/card/add');
			}
			.add-account-span{
				margin-top: 16px;
				font-size: 13px;
				color: #308CF8;
			}
		}
	}
	.bank-card-account:after{
		content: "clear"; 
		display: block; 
		height: 0; 
		clear: both; 
		overflow: hidden; 
		visibility: hidden;
	}
	.bank-card-account + .bank-card-account{
		margin-top: 30px;
	}
}

</style>


