<template>
	<div class="my-bank-card-set-name">
		<mt-field @input="validate" label="请输入别名" placeholder="请输入别名，支持汉字、数字和字母" v-model="alias"></mt-field>
		<div class="button-bottom">
			<submit-button @submit="submit" :submitStatus="submitStatus" size="large" value="确定"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			alias: '',
			id: '',
			submitStatus: false
		};
	},
	methods: {
		validate() {
			if(this.$validate.isEmpty(this.alias)){
				this.submitStatus = false;
			}else{
				this.submitStatus = true;
			}
		},
		submit() {
			service.renameBankCard({
				id: this.id,
				alias: this.alias
			}).then(data => {
				history.back();
			});
		}
	},
	created(){
		this.id = this.$route.params.id;
		this.alias = this.$route.params.alias;
		this.validate();
	}
};

</script>

<style lang="scss">
	
.my-bank-card-set-name{
	.mint-cell:after{
		background-color: transparent;
	}
}

</style>


