<template>
	<div class="my-bank-card-credit-add">
		<div class="field-warn">请输入信用卡卡号</div>
		<mt-field v-model="card" label="信用卡卡号" placeholder="仅限安徽农金系统内的信用卡"></mt-field>
		<div class="field-action">
			<submit-button @click.native="next" :submitStatus="submitStatus" value="下一步"></submit-button>
		</div>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			card: '',
			submitStatus: false
		};
	},
	watch: {
		card: function(val){
			if(val) this.submitStatus = true;
			else this.submitStatus = false;
		}
	},
	methods: {
		next() {
			this.$router.push({name: 'MyBankCardCreditDetail', params: {card: this.card}});
		}
	}
};

</script>


