<template>
	<div class="my-bank-card-add">
		<div class="cell-part">
			<div class="head-hint">
				请输入您需要添加的卡号
			</div>
			<mt-field @input="validate" label="卡号" placeholder="请输入银行卡号" v-model="account"></mt-field>
		</div>
		<div class="cell-part">
			<div class="head-hint">
				请输入交易密码
			</div>
			<mt-field @input="validate" label="交易密码" placeholder="" v-model="password"></mt-field>
		</div>
		<div class="button-bottom">
			<submit-button @submit="submit" :submitStatus="submitStatus" size="large" value="确认添加"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default{
	data() {
		return {
			account: '',
			password: '',
			submitStatus: false
		};
	},
	methods: {
		validate() {
			if(this.$validate.isEmpty(this.account) || this.$validate.isEmpty(this.password)){
				this.submitStatus = false;
			}else{
				this.submitStatus = true;
			}
		},
		submit() {
			service.addBankCard({
				account: this.account,
				password: this.password
			}).then(data => {
				history.back();
			});
		}
	}
};

</script>

<style lang="scss">

.my-bank-card-add{
}

</style>


