<template>
	<div class="my-bank-card-account">
		<div class="account-msg cell-part">
			<div class="name">{{account.name}}</div>
			<div class="bank">
				{{account.bankName}}
				<span class="bank-type">{{account.type}}</span>
			</div>
			<div class="number">{{account.account}}</div>
			<div class="type">
				<div>人民币卡</div>
				<div>账户类型：I类</div>
				<div>账户状态：正常</div>
			</div>
		</div>
		<div class="cell-part">
			<mt-field class="account-rename" label="设置别名" placeholder="（选填）请输入别名，例如：（理财卡）" v-model="rename"></mt-field>
		</div>
		<div class="cell-part">
			<cell-password :options="cellPassword" ></cell-password>
		</div>
		<div class="cell-warn">
			交易密码错误，请重新输入！
		</div>
		<div class="button-bottom">
			<submit-button @submit="submit" :submitStatus="submitStatus" size="large" value="添加到手机银行"></submit-button>
		</div>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			account: {},
			rename: '',
			password: '1111',
			submitStatus: true,
			cellPassword: {
                title: '交易密码',
                rule: 'number'
            }
		};
	},
	methods: {
		validate() {
			if(this.$validate.isEmpty( this.password )){
				this.submitStatus = false;
			}else{
				this.submitStatus = true;
			}
		},
		submit() {
			var _error = [];
			var self = this;
			if( this.$validate.isEmpty( this.password ) ) {
                _error.push( '交易密码不能为空。' );
            }
            if( _error.length > 0 ) {
                Toast({
                    message: _error[0],
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
            service.addBankCardAccount({
            	id: self.account.id,
            	alias: self.account.alias,
            	password: self.account.password
            }).then(data => {
            	this.$router.push({name: 'MyBankCardAddAccountSuccess'});
            });
		},
		getBankCardDetail(id) {
			service.getBankCardDetail({
				id: id
			}).then(data => {
				this.account = data.data;
			});
		}
	},
	created(){
		this.getBankCardDetail(this.$route.params.id);
	}
};

</script>

<style lang="scss">

.my-bank-card-account{
	.account-msg{
		position: relative;
		background-color: #fff;
		padding: 30px 50px 0;
		height: 284px;
		@include image-set('../../../../assets/img/bank/card/bg');
		.name{
			font-size: 15px;
			color: #666666;
			margin-bottom: 20px;
		}
		.bank{
			font-size: 15px;
			color: #666666;
			margin-bottom: 10px;
			.bank-type{
				display: inline-block;
                margin-left: 20px;
                width: 110px;
                height: 36px;
                font-size: 12px;
                color: #B5B6BE;
                text-align: center;
                line-height: 36px;
                @include image-set('../../../../assets/img/bank/card/label');
			}
		}
		.number{
			font-size: 15px;
			color: #272727;
			letter-spacing: 4.28px;
		}
		.type{
			position: absolute;
			left: 0;
			bottom: 20px;
			width: 100%;
			padding: 0 50px;
			display: flex;
			justify-content: space-between;
			font-size: 13px;
			color: #fff;
		}
	}
}

</style>


