<template>
	<div class="my-bank-card-set">
		<div class="account-msg cell-part">
			<div class="name">{{card.name}}</div>
			<div class="bank">
				{{card.bankName}}
				<span class="bank-type">{{card.type}}</span>
			</div>
			<div class="number">{{card.account}}</div>
			<div class="type">
				<div>人民币卡</div>
				<div>账户类型：I类</div>
				<div>账户状态：正常</div>
			</div>
		</div>
		<div class="cell-part">
			<mt-cell title="设置主账户">
  				<a @click="setDefault()" class="default-set" href="javascript:;">设置</a>
  				<!-- <span class="is-default-set">已设置</span> -->
			</mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="setName()" title="设置别名" is-link>
  				<span>{{!card.alias ? '未设置' : card.alias}}</span>
			</mt-cell>
		</div>
		<a @click="deleteCard()" class="account-delete" href="javascript:;">从手机银行删除此卡</a>
	</div>
</template>

<script>
import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/my/my-bank-card';

export default{
	data() {
		return {
			card: {},
			rename: ''
		};
	},
	methods: {
		setDefault() {
			var self = this;
			MessageBox({
			 	title: '您确定要将此银行卡设为主账户',
			  	message: `${self.card.bankName}<br>${self.card.account}`,
			  	showCancelButton: true
			}).then(action => {
				if(action === 'confirm'){
					service.defaultBankCard({
						id: self.card.id
					}).then(data => {
						console.log(data);
					});
				}
			});
		},
		setName() {
			this.$router.push({name: 'MyBankCardSetName'});
		},
		deleteCard() {
			var self = this;
			MessageBox({
			 	title: '您确定要将此卡从手机银行删除？',
			  	message: `${self.card.bankName}<br>${self.card.account}`,
			  	showCancelButton: true
			}).then(action => {
				if(action === 'confirm'){
					service.deleteBankCard({
						id: self.card.id
					}).then(data => {
						this.$router.push({name: 'MyBankCardDeleteSuccess'});
					});
				}
			});
		},
		success() {
			history.back();
		},
		getBankCardDetail(id) {
			service.getBankCardDetail({
				id: id
			}).then(data => {
				this.card = data.data;
			});
		}
	},
	created(){
		this.getBankCardDetail(this.$route.params.id);
	}
};

</script>

<style lang="scss">

.my-bank-card-set{
	.account-msg{
		position: relative;
		background-color: #fff;
		padding: 30px 50px 0;
		height: 284px;
		@include image-set('../../../../assets/img/bank/card/bg');
		.name{
			font-size: 15px;
			color: #666666;
			margin-bottom: 20px;
		}
		.bank{
			font-size: 15px;
			color: #666666;
			margin-bottom: 10px;
			.bank-type{
				display: inline-block;
                margin-left: 20px;
                width: 110px;
                height: 36px;
                font-size: 12px;
                color: #B5B6BE;
                text-align: center;
                line-height: 36px;
                @include image-set('../../../../assets/img/bank/card/label');
			}
		}
		.number{
			font-size: 15px;
			color: #272727;
			letter-spacing: 4.28px;
		}
		.type{
			position: absolute;
			left: 0;
			bottom: 20px;
			width: 100%;
			padding: 0 50px;
			display: flex;
			justify-content: space-between;
			font-size: 13px;
			color: #fff;
		}
	}
	.default-set{
		display: inline-block;
		color: #FF511A;
		font-size: 13px;
		padding: 6px 34px;
		border: 1px solid #FF511A;
		border-radius: 8px;
	}
	.is-default-set{
		font-size: 16px;
		color: #46C300;
	}
	.account-delete{
		display: block;
		padding: 30px 50px;
		margin-top: 80px;
		background-color: #fff;
		font-size: 15px;
		color: #FF511A;
		text-align: center;
	}
}

</style>


