import ajax from '@/services/ajax';
import api from '@/services/api';

export default {
    // 获取已挂取的借记卡，银行卡
    // ACCT_TYPE 1借记卡，3信用卡，5易贷卡 7二三类户
    getCardList: data => {
        return ajax.postAfterLoginQueryList('990600', data, api.getCardList);
    }
};



