<template>
	<div class="my-bank-card-credit-detail">
		<mt-cell title="信用卡卡号" :value="detail.card"></mt-cell>
		<div class="field-warn">请输入卡片信息</div>
		<mt-field v-model="detail.card1" label="CVN2" placeholder=""></mt-field>
		<mt-field v-model="detail.date" label="卡片有效期" placeholder=""></mt-field>
		<mt-field v-model="detail.rename" label="别名" placeholder="（选填）请输入别名，例如：理财卡"></mt-field>
		<cell-password :options=" cellPassword "></cell-password>
		<div class="field-action">
			<mt-button @click="showDialogSmsVerify" type="primary">下一步</mt-button>
			<dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			detail: {
				card: '',
				card1: '',
				date: '',
				rename: ''
			},
			cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入验证码完成添加',
                btnConfirmText: '完成添加',
                btnGetText: '获取验证码'
            }
		};
	},
	methods: {
		showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.addCredit();
            }, (data) => {
            });
        },
        addCredit() {
        	service.addCredit()
        	.then((data) => {
        		this.$router.push({name: 'MyBankCardCreditSuccess'});
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.detail.card = this.$route.params.card;
	}
};

</script>


